@import url('https://fonts.googleapis.com/css2?family=Aref+Ruqaa:wght@400;700&family=Open+Sans:wght@300;400;700&family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300&family=Reem+Kufi:wght@400;600&family=Roboto+Condensed:wght@300;400;700&family=Roboto:wght@100;300;400&display=swap');

*{
margin: 0;
padding: 0;
border: 0;
outline: 0;
box-sizing: border-box;
list-style: none;
text-decoration: none;

}
:root{
    --color-bg:#1f1f38;
    --color-bg-variant:#2c2c6c;
    --color-primary:#4db5ff;
    --color-primary-variant:rgba(77,181,255,0.4);
    --color-white:#fff;
    --color-light:rgba(255,255,255,0.6);
    --transition : all 400ms ease;


    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-ms:90%;





}
html{
    scroll-behavior: smooth;
 

}
::-webkit-scrollbar{
    display: none;
}
body{
   
    font-family: 'Poppins', sans-serif;
    background-color: var(--color-bg);

    line-height: 1.7;
    /* background-image: url('../src/assets/bg-texture.png'); */

}
/* ========================= GENRAL STYLE ===============================*/
.container{
    width: var(--container-width-lg);
    margin: 0 auto;

}
h1,h2,h3,h4,h5,small{
    font-weight: 500;
    margin: 0;
    color: white;
}
h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
    
}
section >h2,section >h5{
    text-align: center;
    color: var(--color-light);
    color: white;
}
section >h2{
    color: var(--color-primary);
    margin-bottom: 3rem;

}
.text-light{
    color: var(--color-light);
}
a{
    color: var(--color-primary);
    transition: var(--transition)  ;


}
a:hover{
    color:var(--color-white)
}
.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.7rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    border: 1px solid var(--color-primary);
}
.btn:hover{
    background-color: var(--color-white);
    color:var(--color-bg);
    border-color: transparent;
}
.btn-primary{
    background-color: var(--color-primary);
    color: var(--color-bg);
}
img{
display: block;
width: 100%;
object-fit: cover;
}
/* ==============  MEDIA QUERISE   (MEDIUM DEVICES)================== */
@media screen and (max-width:1024px) {
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}
/* ==============  MEDIA QUERISE   (SMALL DEVICES)================== */
@media screen and (max-width:600px) {
    .container{
        width: var(--container-width-ms);
    }
    section{
        margin-top: 2rem;
    }
}
